import { assetsURL } from "../../api/LeagueApi";

const SummonerIcon = ({ summoner, width, height }) => {
    return (
        <div className="summoner-icon">
            <img
                src={
                    assetsURL + "profileicon/" + summoner.profileIconId + ".png"
                }
                alt="profile icon"
                width={width}
                height={height}
            />
            <span className="summoner-level">{summoner.summonerLevel}</span>
        </div>
    );
};
export default SummonerIcon;
